<template>
  <div class="content">
    <div>
      <card
          v-for="car in cars"
          :key="car.id"
          :car="car"
          @pushId="pullId"
      ></card>
    </div>
  </div>
</template>

<script>
import card from './CardComponent'
// import {mapActions, mapGetters} from "index"
import axios from "axios";
export default {
  name: "ContentComponent",
  components: {
    card
  },
  computed: {

  },
  props: {
    filters: Object
  },
  methods: {
    pushAllDataTypes() {
      this.$emit('pushAllDataTypes', this.all_data_types)
    },

    pullId(data) {
      alert(`id = ${data}`)
    },
    getCardsData() {
      axios.get("http://127.0.0.1:8000/api/ajax_get_cars/", {params: this.filters})
        //console.log(`filters = ${Object.entries(this.filters)}`)
        .then((response) => {
          console.log(`filters = ${Object.entries(this.filters)}`)
          this.cars = response.data.auto
          console.log(Object.entries(response.data.auto))
          this.all_data_types = response.data.all_data_types
          this.pushAllDataTypes()
        // alert(`response auto = ${response.data.auto[0].model}`)
        // alert(`response all_data_types = ${response.data.all_data_types.model}`)
        })
        .catch((error) => {
          console.log(error)

      })
    }
  },
  watch: {
    filters() {
      this.getCardsData();
    }
  },
  mounted() {
    this.getCardsData();
  },
  data() {
    return {
      cars: [], // full
      all_data_types: [] // full
    }
  }
}
</script>

<style scoped>
body {
  background: #151515;
  color: white;
}
</style>